import React, { useState } from 'react';
import { SystemSettings } from '../../services/settingService';

interface DataProcessingSettingsProps {
  settings: SystemSettings['data_processing'];
  onSave: (settings: SystemSettings['data_processing']) => void;
  saving: boolean;
}

const DataProcessingSettings: React.FC<DataProcessingSettingsProps> = ({ settings, onSave, saving }) => {
  const [formData, setFormData] = useState(settings);

  const handleInputChange = (field: keyof SystemSettings['data_processing'], value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleAggregationRuleChange = (rule: keyof SystemSettings['data_processing']['data_aggregation_rules'], value: boolean) => {
    setFormData(prev => ({
      ...prev,
      data_aggregation_rules: {
        ...prev.data_aggregation_rules,
        [rule]: value
      }
    }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSave(formData);
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/* 실시간 처리 */}
      <div className="space-y-4">
        <h4 className="text-lg font-medium text-gray-900">실시간 처리</h4>
        
        <div className="flex items-center">
          <input
            type="checkbox"
            id="real_time_processing"
            checked={formData.real_time_processing}
            onChange={(e) => handleInputChange('real_time_processing', e.target.checked)}
            className="form-checkbox h-4 w-4 text-blue-600"
          />
          <label htmlFor="real_time_processing" className="ml-2 text-sm text-gray-700">
            실시간 데이터 처리 활성화
          </label>
          <p className="text-sm text-gray-500 ml-2">
            - 데이터가 들어오는 즉시 처리하여 실시간 대시보드 업데이트
          </p>
        </div>
      </div>

      {/* 배치 처리 간격 */}
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-2">
          배치 처리 간격 (초) *
        </label>
        <input
          type="number"
          value={formData.batch_processing_interval}
          onChange={(e) => handleInputChange('batch_processing_interval', parseInt(e.target.value))}
          className="form-input w-full"
          min="60"
          max="86400"
          required
        />
        <p className="text-sm text-gray-500 mt-1">
          60초(1분)에서 86400초(24시간) 사이로 설정하세요
        </p>
      </div>

      {/* 데이터 집계 규칙 */}
      <div className="space-y-4">
        <h4 className="text-lg font-medium text-gray-900">데이터 집계 규칙</h4>
        <p className="text-sm text-gray-600">
          어떤 주기로 데이터를 집계할지 선택하세요.
        </p>
        
        <div className="grid grid-cols-2 gap-4">
          <div className="flex items-center">
            <input
              type="checkbox"
              id="hourly_aggregation"
              checked={formData.data_aggregation_rules.hourly}
              onChange={(e) => handleAggregationRuleChange('hourly', e.target.checked)}
              className="form-checkbox h-4 w-4 text-blue-600"
            />
            <label htmlFor="hourly_aggregation" className="ml-2 text-sm text-gray-700">
              시간별 집계
            </label>
          </div>

          <div className="flex items-center">
            <input
              type="checkbox"
              id="daily_aggregation"
              checked={formData.data_aggregation_rules.daily}
              onChange={(e) => handleAggregationRuleChange('daily', e.target.checked)}
              className="form-checkbox h-4 w-4 text-blue-600"
            />
            <label htmlFor="daily_aggregation" className="ml-2 text-sm text-gray-700">
              일별 집계
            </label>
          </div>

          <div className="flex items-center">
            <input
              type="checkbox"
              id="weekly_aggregation"
              checked={formData.data_aggregation_rules.weekly}
              onChange={(e) => handleAggregationRuleChange('weekly', e.target.checked)}
              className="form-checkbox h-4 w-4 text-blue-600"
            />
            <label htmlFor="weekly_aggregation" className="ml-2 text-sm text-gray-700">
              주별 집계
            </label>
          </div>

          <div className="flex items-center">
            <input
              type="checkbox"
              id="monthly_aggregation"
              checked={formData.data_aggregation_rules.monthly}
              onChange={(e) => handleAggregationRuleChange('monthly', e.target.checked)}
              className="form-checkbox h-4 w-4 text-blue-600"
            />
            <label htmlFor="monthly_aggregation" className="ml-2 text-sm text-gray-700">
              월별 집계
            </label>
          </div>
        </div>
      </div>

      {/* 데이터 정리 스케줄 */}
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-2">
          데이터 정리 스케줄 (Cron 표현식) *
        </label>
        <input
          type="text"
          value={formData.data_cleanup_schedule}
          onChange={(e) => handleInputChange('data_cleanup_schedule', e.target.value)}
          className="form-input w-full"
          placeholder="0 2 * * *"
          required
        />
        <p className="text-sm text-gray-500 mt-1">
          Cron 표현식 형식: 분 시 일 월 요일 (예: 0 2 * * * = 매일 새벽 2시)
        </p>
        <div className="mt-2 text-xs text-gray-500">
          <p>• 분: 0-59</p>
          <p>• 시: 0-23</p>
          <p>• 일: 1-31</p>
          <p>• 월: 1-12</p>
          <p>• 요일: 0-6 (0=일요일)</p>
        </div>
      </div>

      {/* 저장 버튼 */}
      <div className="flex justify-end pt-6 border-t border-gray-200">
        <button
          type="submit"
          disabled={saving}
          className="btn-primary"
        >
          {saving ? (
            <>
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
              저장 중...
            </>
          ) : (
            '설정 저장'
          )}
        </button>
      </div>
    </form>
  );
};

export default DataProcessingSettings; 